.Lessee {
    display: flex;
    flex-direction: row;
    min-height: 100vh;
    width: 100vw;
    box-sizing: border-box;
    /* background-color: #f5f7fa #c3cfe2; */
    margin-top: 43px;
    background-image: linear-gradient(to bottom, #f5f7fa, #c3cfe2);
    cursor: default;
  }
  
  .LesseeBar {
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: center;
    height: 100vh;
    width: 250px;
    background-color: #d8e2f4;
    box-sizing: border-box;
    position: fixed;
  }
  .List {
    display: flex;
  }
  .LesseesList {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin-top: 8px;
  }
  .LesseesList > p {
    font-weight: 500;
    font-size: 25px;
    letter-spacing: 1px;
    text-align: right;
    margin-right: 60px;
    color: rgb(126, 136, 172);
  }
  
  .Return {
    margin-top: -5px;
    margin-bottom: 10px;
    text-align: left;
    height: 40px;
    color: black;
    margin-left: 5px;
  }
  .Return > p {
    margin-top: -26px;
    text-align: left;
    height: 50px;
    color: black;
    margin-left: 55px;
    font-size: 8px;
    padding-left: -5px;
    font-family: "Alata", sans-serif;
  }
  
  .Return > button {
    color: black;
    background-color: transparent;
    border: none;
    padding: 10px;
    transition: background-color 0.5s;
    border-radius: 25px;
  }
  
  .ListItem {
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: center;
    margin-left: -5px;
    margin-top: 50px;
  }
  
