  .ListItem > button {
    background-color: transparent;
    border: none;
    padding: 2px;
    width: 200px;
    margin-left: -25px;
    align-items: center;
    text-align: center;
    justify-content: center;
  }
  .LO {
    display: flex;
    justify-content: right;
    align-items: center;
    flex-direction: row;
    width: 250px;
  }
  .LO > p {
    text-align: right;
    font-size: 14px;
    font-weight: 600;
    /* margin-top: 160px;
    margin-right: 50px; */
  }
  .Search {
    width: 200px;
    align-items: center;
    /* margin: 50px 0px -50px 30px; */
  }
  .Search > input {
    border-radius: 15px;
    padding: 5px;
    /* margin-top: 8px; */
    width: 200px;
    border: none;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
      rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
    text-align: center;
    font-family: "Alata", sans-serif;
    font-size: 12px;
  }
  .Search > input:focus{
    outline: none;
  }
  .See > p{
    display: flex;
    justify-content: right;
    text-align: right;
    font-size: 12px;
    font-weight: 600;
    margin-bottom: -15px;
    margin-left: 500px ;
    cursor: pointer;
    
  }
  .Tables {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    /* flex-grow: 0.4; */
    display: flex;
  }
  .TableContent {
    /* background-color: antiquewhite; */
    justify-content: right;
    align-items: center;
    box-sizing: border-box;
    /* min-width: 200px;
    height: 100vh; */
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin-top: 80px;
  }
  
  .TableContent > table {
    width: 580px;
    text-align: center;
    /* margin-left: 10px; */
    margin-top: 20px;
    /* background-color: #fdfcfb; */
  }
  .TableContent > td:nth-child(even) {
    width: 580px;
    text-align: center;
    /* margin-left: 10px; */
    margin-top: 20px;
    background-color: #fdfcfb;
  }
  tr > td > button {
    display: flex;
    flex-direction: row;
    height: 14px;
    width: 15px;
    font-size: 8px;
    font-weight: bold;
    padding: 1px;
    margin: 1px;
    border: none;
    text-align: center;
    /* border-radius: 2px;
    background-color: rgb(155, 177, 211); */
  }
  .view {
    height: 14px;
    width: 15px;
    font-size: 8px;
    font-weight: bold;
    text-align: center;
    align-items: center;
    justify-content: center;
    padding: 1px;
    margin: 1px;
    /* border-radius: 2px; */
    border: none;
    /* background-color: rgb(86, 140, 222); */
  }
