  /*Details*/
  .Details {
    display: flex;
    flex-direction: column;
    align-content: center;
    margin-left: 50px;
    margin-top: 70px;
    background-color: f5f7fa;
    width: 380px;
    height: 500px;
    box-sizing: border-box;
  }
  .DetailsContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: left;
    margin-right: 40px;
    margin-top: 25px;
  }
  .DetailsContent > h2 {
    margin-bottom: 30px;
  }
  
  .DetailsForm {
    margin: 8px;
    margin-left: -20px;
    display: flex;
    flex-direction: row;
  }
  
  .DetailsForm > input {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    width: 350px;
    font-size: 14px;
  }
  
  .DetailsForm :focus {
    outline: none;
  }
  .DetailsForm1 :focus {
    outline: none;
  }
  
  .DetailsForm1 {
    margin: 8px;
    display: flex;
    flex-direction: row;
    margin-left: -27px;
    margin-top: 20px;
  }
  .DetailsForm1 > input {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    width: 350px;
    font-size: 14px;
  }
  .DetailsForm2 {
    margin-left: 1px;
    margin-right: 3px;
  }
  .DetailsForm2 > input {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    width: 172px;
    margin-left: 1px;
    font-size: 14px;
  }
  .AppStatus {
    margin-left: 10px;
    margin-right: 8px;
  }
  .AppStatus > input {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    width: 150px;
    margin-left: 6px;
    font-size: 14px;
  }
  .CancelDetails {
    color: red;
    justify-content: left;
    align-items: center;
    text-align: left;
    margin-left: -15px;
  }
  .DetailsButton {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 20px;
  }
  .AddDetails {
    color: black;
    justify-content: right;
    align-items: center;
    text-align: right;
    margin-right: 20px;
  }
  
  .DetailsButton > button {
    background-color: transparent;
    border: none;
    padding: 10px;
    transition: background-color 0.5s;
    border-radius: 25px;
  }
