.archive {
  background-size: cover;
  background-repeat: no-repeat, repeat;
  background-position: center;
  background-image: url("../../assets/MarketName.jpg");
  opacity: 1;
  /* margin-left: 90px ; */
  position: absolute;
  z-index: 1;
} /* background-color: #f1f4fb; */
.BgContent {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100vw;
  background-color: #21478da4;
}

.search {
  display: flex;
  justify-content: center;
  align-items: center;
  /* width: 200px; */
  align-items: center;
  margin: 125px 0px 25px 210px;
}
.search > input {
  border-radius: 15px;
  padding: 5px;
  /* margin-top: 8px; */
  width: 250px;
  border: none;
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
    rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
  text-align: center;
  font-family: "Alata", sans-serif;
  font-size: 12px;
  background-color: white;
}
.search > input:focus {
  outline: none;
}
.archiveContent {
  background-color: rgba(245, 245, 245, 0.598);
  display: flex;
  flex-direction: column;
  margin: 25px;
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
    rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
  margin-left: 240px;
  border-radius: 15px;
  backdrop-filter: blur(5px);
  margin-top: 1px;
}
.contents {
  display: flex;
  margin: 25px;
}
.tables {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tables > p {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  font-family: "Alata", sans-serif;
  color: #0c0c0ceb;
  letter-spacing: 1px;
}

td > div {
  font-size: 12px;
  color:  #7d8085;
}
.contents > table :hover{
  background-color: #ddd;
}