  .BackA {
    margin-top: -5px;
    text-align: left;
    height: 50px;
    color: white;
    margin-left: 5px;
  }
  
  .BackA > button {
    background-color: transparent;
    border: none; 
     margin-top: 16px;
    height: 50px;
    color: black;
    text-align: left;
   
  }
  .Name > h1 {
    text-align: center;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    font-size: 30px;
    color: #98a1ad;
  }
  
  .InputContainerE {
    flex-direction: column;
    display: flex;
    margin-top: 70px;
    align-items: center;
  }
  .InputE {
    width: 300px;
    align-items: center;
    justify-content: center;
    text-align: center;
    display: flex;
    margin-top: 25px;
    background-color: #d5e0f736;
    border-radius: 10px;
    box-shadow: none;
    box-sizing: border-box;
    background: transparent;
    border-bottom: 1.5px solid black;
    width: 350px;
    height: 50px;
  }
  
  .InputE > input {
    background-color: transparent;
    border: none;
    text-align: center;
    width: 260px;
    padding: 2px;
    font-size: 18px;
    font-weight: 600;
    z-index: 100;
    display: flex;
    color: black;
  }
  
  .InputE > input:focus {
    outline: none;
  }
  .InputE > input::placeholder {
    font-size: 16px;
    font-weight: 600;
  }
  
  .Confirm1 {
    color: black;
    text-align: center;
    margin-top: 180px;
  }
  
  .Confirm1 > button {
    /* background-color: #77a3f3; */
    background-image: linear-gradient(#020841e7, #1323b3e6);
    border: none;
    color: white;
    font-size: 18px;
    font-weight: 300;
    font-family: "Alata", sans-serif;
    transition: background-color 0.5s;
    border-radius: 25px;
    justify-content: center;
    text-align: center;
    width: 200px;
    height: 45px;
  }
  
  .Confirm1 > button:active {
    background-color: rgb(0, 157, 255);
    padding: 8px;
  }
  